@import "@theme/vars.scss";

.filter{
    display: flex;
    flex-grow: 1;
    
    @media only screen and (max-width: $bp-lg) {
        flex-wrap: wrap;
        justify-content: space-between;
    }

    form{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        flex-grow: 1;
        margin: 0 -4px -8px;
        max-width: calc(100% + 8px);

        @media only screen and (max-width: $bp-md) {
            flex-wrap: wrap;
        }

        & > *{
            margin: 0 4px 8px;
        }
    }

    &__calendar + .badges{
        width: calc(100% - 290px);

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            padding-top: 8px;
            border-top: 1px solid #D1D5DB;
        }
    }

    &__search{
        width: 100%;
        flex-grow: 1;
        display: flex;
        align-items: center;
        flex-wrap: nowrap;

        .input{
            max-width: none;
            flex-grow: 1;
        }

        .button{
            min-width: 120px;
            height: 100%;
            margin-left: 16px;
            flex-shrink: 0;
        }
    }

    &__calendar{
        width: 260px;
        flex-shrink: 0;
        margin-left: 30px;
        order: 2;

        @media only screen and (max-width: $bp-md) {
            width: calc(100% - 8px);
            order: 0;
            max-width: none;
        }
    }

    &__datepicker,
    &__events {
        width: 100%;
        margin-bottom: 32px !important;
        max-width: calc(100% - 8px);

        &:last-child{
            margin-bottom: 8px !important;
        }
    }
}