@import "@theme/vars.scss";

.card-book {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 16px;
    background-color: transparent;
    text-decoration: none;
    color: $dark;
    overflow: hidden;
    border: 1.5px solid $light;
    transition: background-color 0.45s ease, color 0.45s ease, border-color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-xl) {
        padding: 20px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 16px;
    }

    &:hover{
        background-color: $light;
        // border-color: $light-medium;
    }

    &:hover &__img-wrapper img{
        transform: scale(103%);
    }

    &__img-wrapper{
        cursor: pointer;
        position: relative;
        text-decoration: none;
        color: $dark;
        display: flex;  
        width: 100%;
        height: 280px;
        margin-bottom: 16px;
    }

    &__img {
        display: flex;
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__id{
        position: absolute;
        bottom: 12px;
        right: 0;
        color: #9CA3AF;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        z-index: 1;
    }

    &__main {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        align-content: flex-start;
        margin-bottom: 8px;
    }

    &__inner{
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        overflow: hidden;
    }

    &__labels-wrapper{
        display: flex;
        align-items: center;
        flex-wrap: wrap-reverse;
        margin: 0 -4px;
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        overflow: hidden;
        max-width: 100%;
        margin: 0 -4px;
    }
  
    &__label {
        position: relative;
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 24px;
        padding: 0 8px;
        border-radius: 50px;
        border: 1px solid $orange;
        color: $orange;
        fill: $orange;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        margin: 0 4px 8px;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        transition: color 0.35s ease, border-color 0.35s ease;

        &:nth-last-child(1),
        &:nth-last-child(2){
            flex-shrink: 1;
        }
        &:first-of-type{
            flex-shrink: 0;
            background-color: $orange;
            color: #fff;
            fill: #fff;
        }

        .icon{
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;
        }
    }
  
    .like{
        position: static;
        margin-left: 8px;
        margin-bottom: auto;
        stroke: $blue;

        .icon{
            width: 24px;
            height: 24px;
        }

        &:active{
            stroke: $blue;
        }
    
        &.is-favorite{
            stroke: #EC7D42;
    
            &:hover{
                stroke: #BA531A;
            }
        
            &:active{
                stroke: #A23F02;
            }
        }
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        font-weight: 700;
        font-size: 18px;
        line-height: 24px;
        margin-right: auto;
        width: calc(100% - 32px);
        margin-bottom: 8px;
        transition: color 0.35s ease;

        &:hover{
            color: $blue;
        }
    }

    &__details{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 100%;
    }

    &__author{
        width: 100%;
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        color: #1890FF;
        text-decoration: none;
        margin-bottom: 4px;
    }

    &__info{
        margin-bottom: 8px;

        &>span{
            width: 100%;
            font-size: 14px;
            line-height: 100%;
            font-weight: 600;
            display: inline-flex;
            color: #9CA3AF;
        }

        p{
            font-size: 14px;
            line-height: 100%;
            font-weight: 600;
            display: inline;
            color: #9CA3AF;

            span{
                color: #1890FF;
                text-decoration: none;
            }
        }
    }

    &__text {
        width: 100%;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 2;
        font-family: 'Literata';
        font-size: 14px;
        line-height: 22px;
        color: $gray-blue;
    }

    &__button{
        margin-top: auto;
        width: 100%;
    }

    &--long,
    &--line {
        padding: 0 0 0 200px;
        flex-direction: row;

        @media only screen and (max-width: $bp-md) {
            padding: 16px;
            flex-direction: column;
        }
    }

    &--long &__img-wrapper,
    &--line &__img-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        width: 200px;
        height: 100%;
        background-color: #fff;
        margin-bottom: 0;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            background-color: transparent;
            margin-bottom: 16px;
            width: 100%;
        }
    }

    &--long &__inner,
    &--line &__inner {
        padding: 16px;

        @media only screen and (max-width: $bp-md) {
            padding: 0;
        }
    }

    &--long &__inner,
    &--line &__inner {
        width: auto;
    }

    &--long &__button,
    &--line &__button {
        width: auto;
        min-width: 150px;

        @media only screen and (max-width: $bp-md) {
            min-width: auto;
            width: 100%;
        }
    }


    &--line {
        .like{
            order: -1;
        }
    }

    &--line &__title{
        width: auto;
        margin-right: 8px;
        font-size: 16px;
        line-height: 28px;
    }

    &--line &__labels{
        width: calc(100% - 32px);
        order: -1;
    }

    &--line &__author{
        width: auto;
        font-size: 12px;
        line-height: 12px;
        margin-right: 16px;
        margin-bottom: 8px;
    }

    &--line &__info {
        font-size: 12px;
        line-height: 12px;
        
        p{
            margin-right: 4px;
        }

        &>span{
            width: auto;
        }

        &>*:last-child{
            margin-right: 0;
        }
    }
}
