@import "@theme/vars.scss";

.account {
    padding-right: 12px;
    padding-left: 12px;

    @media only screen and (max-width: $bp-lg) {
        padding-right: 0;
        padding-left: 0;
    }

    .section{
        &__inner{
            &>*:last-child{
                margin-bottom: 0;
            }
        }
    }

    &__main {
        margin-bottom: 32px;
    }

    &__roles {
        margin-bottom: 24px;
    }

    &__title {
        color: $dark;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }
    }

    &__favorite {
        margin-bottom: 24px;
    }
    
    &__edit {
        margin-bottom: 24px;
    }

    &__actions {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-wrap: wrap;
        margin: 0 -8px -12px;

        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: -8px;
        }
    }

    &__action {
        margin: 0 8px 12px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 8px;
        }
    }

    &__roles {
    }
}

.account-main {
    box-sizing: border-box;

    .account{
        &__actions {
            max-width: 50%;
            margin-left: -12px;
            margin-right: -12px;

            @media only screen and (max-width: $bp-md) {
                max-width: none;
                margin-right: -8px;
                margin-left: -8px;
                width: auto;
            }
        }
    
        &__action {
            margin-right: 12px;
            margin-left: 12px;

            @media only screen and (max-width: $bp-lg) {
                margin-left: 8px;
                margin-right: 8px;
            }

            &:not(.button--icon){
                @media only screen and (max-width: $bp-lg) {
                    flex-grow: 1;
                }
            }

            &--logout{
                @media only screen and (max-width: $bp-md) {
                    display: none;
                }
            }
        }
    }

    &__bg {
        display: flex;
        height: 250px;
        min-width: 100%;
        margin: 0 -32px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0;
            width: 100%;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: -16px;

        @media only screen and (max-width: $bp-md) {
            margin-top: -88px;
            flex-direction: column;
            align-items: initial;
        }
    }

    &__user {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            margin-bottom: 16px;
        }
    }

    &__img-wrapper{
        display: flex;
        align-items: center;
        flex-shrink: 0;
        position: relative;
        margin-right: 12px;

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
            margin-bottom: 8px;
        }

        .account__action--logout{
            display: none;

            @media only screen and (max-width: $bp-md) {
                display: flex;

                width: 44px;
                height: 44px;
                position: absolute;
                bottom: 0;
                right: -16px;

                .button__icon{
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }

    &__img {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 156px;
        height: 156px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 4px solid $light;
        box-shadow: 0px 25px 50px -12px rgba(16, 24, 40, 0.1);
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            width: 135px;
            height: 135px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 120px;
            height: 120px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
    }

    &__title {
        color: $dark-light;
        margin-bottom: 12px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -12px -12px;

        @media only screen and (max-width: $bp-md) {
            justify-content: center;
        }
    }

    &__list-item {
        display: inline-flex;
        align-items: center;
        min-height: 32px;
        margin-left: 12px;
        margin-bottom: 12px;

        &::after{
            content: "";
            display: inline-flex;
            height: 24px;
            width: 1px;
            background-color: #E5E7EB;
            margin-left: 12px;
        }

        &:last-of-type{
            margin-right: 12px;
            
            &:after{
                content: none;
            }
        }

        strong{
            color: $dark-light;
            margin-right: 8px;
        }

        span{
            color: $light-medium;
        }
    }

}

.account-favorite {

    &__badges {
        margin-bottom: 16px;
        
        .badge{
            height: 40px;
            font-size: 16px;
            font-weight: 600;

            &__wrapper{
                padding: 0 18px;

                .icon{
                    width: 24px;
                    height: 24px;
                }

                &>span span{
                    color: #D1D5DB;
                }
            }
        }
    }

    &__tabs {
    }

    &__content {
        padding: 24px;
        background-color: $light;

        .grid {

            &__block {
            }
        
            &__list {
                row-gap: 24px;
            }
        
            &__more {
                font-size: 16px;
            }
        }
    }
}

.account-edit {

    .account{
        &__actions{
            @media only screen and (max-width: $bp-md) {
                flex-grow: 1;
                min-width: 100%;
                margin-bottom: 12px;
                width: auto;
                order: -1;
            }
        }

        &__action{
            flex-grow: 1;

            @media only screen and (max-width: $bp-md) {
                height: 40px;
            }
        }

        &__title{
            @media only screen and (max-width: $bp-md) {
                font-size: 18px;
            }
        }
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-width: 100%;
        margin: 0 -32px 32px;
        padding-bottom: 16px;
        border-bottom: 1px solid rgba(76, 78, 100, 0.12);

        @media only screen and (max-width: $bp-xxl) {
            margin: 0 0 32px;
            width: 100%;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-bottom: 8px;
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-wrap: wrap;
        }

    }

    &__title {

        @media only screen and (max-width: $bp-md) {
            font-size: 20px;    
        }
    }

    &__content {
        padding-bottom: 32px;
        border-bottom: 1px solid rgba(76, 78, 100, 0.12);

        @media only screen and (max-width: $bp-lg) {
            padding-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            padding-bottom: 0;
            border-bottom: 0;
        }
    }
    

    &__form {
        display: flex;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__aside{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 180px;
        flex-shrink: 0;
        margin-right: 32px;

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
            margin-bottom: 16px;
        }
    }

    &__inputs {
        display: block;
        column-count: 2;
        column-gap: 32px;
        flex-grow: 1;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            display: flex;
            flex-direction: column;
        }
    }

    &__input-button{
        width: 100%;
        margin-top: 16px;

        @media only screen and (max-width: $bp-md) {
            width: auto;
        }
    }

    &__input-img {
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        flex-shrink: 0;
        fill: $light;

        &:hover{
            .icon{
                bottom: 32px;
            }
        }

        input{
            display: none;
        }

        .icon{
            position: absolute;
            left: 50%;
            bottom: 24px;
            width: 20px;
            height: 20px;
            transform: translateX(-50%);
            z-index: 2;
            transition: bottom 0.35s ease;
        }
    }

    &__img {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 156px;
        height: 156px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 4px solid $light;
        box-shadow: 0px 25px 50px -12px rgba(16, 24, 40, 0.1);
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__input {
        display: flex;
        flex-direction: column;
        break-inside: avoid;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            &:last-of-type{
                margin-bottom: 0;
            }
        }

        .input{
            max-width: none;
        }
    }

    &__input-title {
        color: #6B7280;
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 4px;
    }
}